.main{
    display:flex;
    background: #ccced0;
}

.grid{
    display:grid;
     grid-template-columns: repeat(200px,minmax(0,1fr));  
  
    gap: 20px;
    flex-shrink: 1;
    margin-top: 5%;
    margin-left:5%;
    width: 60%;
    height: 100%;
    /* justify-content: space-between; */
    flex-wrap: wrap;
    


}
.column{
display: flex;
flex-direction:column; 
overflow: hidden;
gap:1.125rem;
flex-shrink: 0;
flex-basis: 800px;
flex-wrap: wrap;
/* margin-top: 20px;
margin-bottom: 20px; */

}
.FAQ{
    display: flex;
    flex-direction: column;
        padding: 35px 70px 58px 79px;
        justify-content: flex-end;
        align-items: start;
        flex: 0 0 800;       
        border-radius: 16px;
    background: #FFF;
    flex-grow: 1;
    gap:25px;
   
}
.text{
    display: flex;
    flex-direction: column;
    align-items: start;
}
.line{
    width: 78%;
  
border-bottom: 1px solid rgba(201, 207, 221, 0.60);
}
.Header{
   font-weight: 700;
   font-size: 30px;
    
}
@media (max-width: 925px) {
    .grid {
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }
  }